.accentBalls {
  display: flex;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.logoHolder {
  z-index: 2;
  position: absolute;
  top: 0;
  margin: auto;
  width: 100%;
}

.mainContainer {
  display: flex;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.verifyContainer {
  position: relative;
  width: 40%;
  height: 50%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  top: 50px;

  background: linear-gradient(
    151.76deg,
    rgba(231, 222, 255, 0.24) -18.46%,
    rgba(231, 222, 255, 0) 105.7%
  );
  backdrop-filter: blur(25px);
  /* Note: backdrop-filter has minimal browser support */
  border-radius: 40px;
  margin-top: 110px;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  color: white;
  width: 70%;
}

label {
  width: 100%;
  align-items: center;
}

input {
  display: flex;
  flex-direction: column;
  height: 10%;
}

.phoneInput {
  width: 100%;
  height: 50px;
  background: rgba(80, 80, 80, 0.5);
  outline: none;
  border: none;
  border-radius: 30px;
  padding-left: 29px;
  font-size: 25px;
  margin-top: -10px;
  font-family: "Inter";
  font-weight: 400;
}

.phoneInput,
select,
textarea {
  color: white;
}

textarea:focus,
.phoneInput:focus {
  color: white;
}

.submitButton {
  width: 60%;
  height: 50px;
  border-radius: 70px;
  background: linear-gradient(90deg, #9617dd 0%, #d60bcd 100%);
  border-radius: 70px;
  outline: none;
  border: none;
  margin: auto;
  margin-top: 20px;
  font-family: "Inter";
  font-weight: 600;
  font-size: 20px;
  color: white;
  align-items: center;
  justify-content: center;
}

.title {
  position: relative;
  top: 0;
  margin: 0;

  font-family: "Inter";
  font-style: italic;
  font-weight: 800;
  font-size: 150px;

  background: linear-gradient(180deg, #9617dd 33.16%, #df09ca 103.74%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  font-family: "Inter", sans-serif;
  font-weight: 900;
}

.subtitle {
  position: relative;
  top: 0;
  margin-top: -30px;

  font-style: italic;
  font-size: 40px;
  color: white;
  font-family: "Inter", sans-serif;
  font-weight: 700;
}

.numberTxt {
  font-family: "Inter";
  font-weight: 600;
  margin-left: 20px;
}

.createAccountTxt {
  margin-top: 70px;
  color: white;
  font-family: "inter";
  font-weight: 600;
}

